.c
{
    height: 100vh;
    position: relative;
}
.c-bg
{
width: 20px;
height: 100%;
background-color: #59b256;
position: absolute;
}
.c-wrapper
{
    padding: 50px;
    display: flex;
}
.c-left
{
    flex: 1;
}
.c-right
{
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.c-right p
{
    font-size: 25px;
}
.c-title
{
    font-size: 60px;
    width: 80%;
    color: red;
}
.c-info-item
{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}
.c-info-item img
{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
form
{
    margin-top: 20px;
}
input
{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea
{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button
{
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
    cursor: pointer;
}
/* icons */
.icons
{
    position: relative;
    bottom: 100px;
}
.icons img
{
    height: 50px;
    width: 50px;
    margin-left: 80px;
    margin-top: 40px;
    
}
.connect
{
    position: relative;
    bottom: 80px;
    margin-left: 150px;
}
